const AudioPlayer = () => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTrack, setCurrentTrack] = useState(0);
  const audioRef = useRef<HTMLAudioElement>(null);

  const tracks = ["/music/unlocked_fate.wav", "/music/we_are_monshape.wav"];

  const playNext = () => {
    const nextTrack = (currentTrack + 1) % tracks.length;
    setCurrentTrack(nextTrack);
    if (audioRef.current) {
      audioRef.current.src = tracks[nextTrack];
      if (isPlaying) {
        audioRef.current.play();
      }
    }
  };

  const togglePlay = () => {
    if (audioRef.current) {
      if (isPlaying) {
        audioRef.current.pause();
      } else {
        audioRef.current.play();
      }
      setIsPlaying(!isPlaying);
    }
  };

  useEffect(() => {
    const audio = audioRef.current;
    if (!audio) return;

    const handleEnded = () => {
      playNext();
    };

    audio.addEventListener("ended", handleEnded);
    return () => {
      audio.removeEventListener("ended", handleEnded);
    };
  }, [currentTrack]);

  return (
    <div className="fixed w-[342px] bottom-[60px] bg-white/10 border rounded-xl border-white/30 h-[57px] left-[60px] z-50 backdrop-blur-sm">
      <div className="relative pl-[90px] flex items-center justify-between gap-2 p-2 ">
        <img
          src="/background/music_player.gif"
          alt="Music"
          className="w-[126px] h-[173px] absolute bottom-1/2 translate-y-1/2 translate-x-1/2 -left-[115px]"
        />
        <audio ref={audioRef} src={tracks[currentTrack]} />
        <div className="flex flex-col justify-center -mt-[3px]">
          <p className="text-white text-base uppercase">Song name</p>
          <p className="text-white/50 text-sm">
            {currentTrack ? "We Are Monshape" : "Unlocked Fate"}
          </p>
        </div>
        <button
          onClick={togglePlay}
          className="w-10 h-10 flex items-center justify-center rounded-full bg-[#A314B4] hover:bg-opacity-80 transition-all"
        >
          {isPlaying ? (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M15.75 5.25v13.5m-7.5-13.5v13.5"
              />
            </svg>
          ) : (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M5.25 5.653c0-.856.917-1.398 1.667-.986l11.54 6.348a1.125 1.125 0 010 1.971l-11.54 6.347a1.125 1.125 0 01-1.667-.985V5.653z"
              />
            </svg>
          )}
        </button>
        <button
          onClick={playNext}
          className="w-10 h-10 flex items-center justify-center rounded-full bg-[#A314B4] hover:bg-opacity-80 transition-all"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M3 8.689c0-.866.66-1.567 1.474-1.567h2.21c.791 0 1.474.7 1.474 1.567v6.662c0 .866-.683 1.567-1.474 1.567H4.474c-.814 0-1.474-.7-1.474-1.567V8.69zM12.426 8.689c0-.866.66-1.567 1.474-1.567h2.21c.791 0 1.474.7 1.474 1.567v6.662c0 .866-.683 1.567-1.474 1.567h-2.21c-.814 0-1.474-.7-1.474-1.567V8.69z"
            />
          </svg>
        </button>{" "}
      </div>
    </div>
  );
};

export function NFT() {
  const { address, chainId, isDisconnected } = useAccount();
  const { switchChainAsync } = useSwitchChain();
  const isWrongNetwork = chainId !== 10143;
  const [open, setOpen] = useState(false);
  const [mintingStep, setMintingStep] = useState<
    "idle" | "preparing" | "confirming" | "success" | "error"
  >("idle");
  const [, setLastMintedNFT] = useState<{
    id: string;
    image: string;
  } | null>(null);
  const loadingTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const successTimeoutRef = useRef<NodeJS.Timeout | null>(null);

  const {
    maxSupply,
    totalMinted,
    isPaused,
    mint,
    isSuccess: isMintSuccess,
    userMintStatus,
    mintPhaseInfo,
    refreshUserNFTs,
    lastMintedTokenId,
    isUserWL,
    isUserFCFS,
    isUserTeam,
  } = useNFT();

  const canCurrentlyMint = userMintStatus?.canCurrentlyMint;
  const userMints = userMintStatus?.mintsDone || 0;
  const maxMintsPerAddress = userMintStatus?.mintsAllowed || 1;
  const userStatusInfo = userMintStatus?.userStatus || "";
  const isWhitelisted =
    userStatusInfo.includes("WHITELIST") || userStatusInfo.includes("OG");
  const whitelistOnly =
    mintPhaseInfo?.currentPhase === "WHITELIST" ||
    mintPhaseInfo?.currentPhase === "OG_SALE";

  const handleSwitchNetwork = async () => {
    try {
      await switchChainAsync({ chainId: 10143 });
    } catch (error) {
      console.error("Network switching error:", error);
    }
  };

  const handleMint = async () => {
    try {
      setMintingStep("preparing");

      if (chainId !== 10143) {
        await handleSwitchNetwork();
        return;
      }

      const result = await mint(false);
      setMintingStep("confirming");

      if (result && result.success) {
        setMintingStep("success");

        if (successTimeoutRef.current) {
          clearTimeout(successTimeoutRef.current);
        }

        successTimeoutRef.current = setTimeout(() => {
          setMintingStep("idle");
          successTimeoutRef.current = null;
        }, 3000);

        setTimeout(() => {
          if (lastMintedTokenId) {
            setLastMintedNFT({
              id: String(lastMintedTokenId),
              image: "/placeholder-nft.png",
            });
          } else {
            checkForNFTMetadata();
          }

          if (successTimeoutRef.current) {
            clearTimeout(successTimeoutRef.current);
            successTimeoutRef.current = null;
          }
        }, 1000);
      }
    } catch (error) {
      console.error("Mint error:", error);
    }
  };

  const [attempts, setAttempts] = useState(0);
  const MAX_ATTEMPTS = 2;

  const checkForNFTMetadata = async () => {
    if (attempts >= MAX_ATTEMPTS) {
      if (lastMintedTokenId) {
        setLastMintedNFT({
          id: String(lastMintedTokenId),
          image: "/placeholder-nft.png",
        });
      } else if (totalMinted) {
        const tokenId = Number(totalMinted) - 1;
        setLastMintedNFT({
          id: String(tokenId),
          image: "/placeholder-nft.png",
        });
      }
      return;
    }

    setAttempts((prev) => prev + 1);

    try {
      await refreshUserNFTs();

      if (lastMintedTokenId) {
        setLastMintedNFT({
          id: String(lastMintedTokenId),
          image: "/placeholder-nft.png",
        });
        return;
      }

      if (totalMinted) {
        const tokenId = Number(totalMinted) - 1;
        setLastMintedNFT({
          id: String(tokenId),
          image: "/placeholder-nft.png",
        });
      } else {
        setLastMintedNFT({
          id: "?",
          image: "/placeholder-nft.png",
        });
      }
    } catch (error) {
      console.error("Error checking NFT metadata:", error);
      setLastMintedNFT({
        id: lastMintedTokenId ? String(lastMintedTokenId) : "?",
        image: "/placeholder-nft.png",
      });
    }
  };

  useEffect(() => {
    if (isMintSuccess) {
      setMintingStep("success");

      if (successTimeoutRef.current) {
        clearTimeout(successTimeoutRef.current);
      }

      successTimeoutRef.current = setTimeout(() => {
        setMintingStep("idle");
        successTimeoutRef.current = null;
      }, 3000);
    }
  }, [isMintSuccess]);

  useEffect(() => {
    return () => {
      if (loadingTimeoutRef.current) {
        clearTimeout(loadingTimeoutRef.current);
      }

      if (successTimeoutRef.current) {
        clearTimeout(successTimeoutRef.current);
      }
    };
  }, []);

  const isSoldOut = totalMinted >= maxSupply;
  const userCanMint =
    canCurrentlyMint && !isPaused && (!whitelistOnly || isWhitelisted);

  useEffect(() => {
    const hasSeenPopup = localStorage.getItem("hasSeenCollectionInfoPopup");
    if (hasSeenPopup) {
    }
  }, []);

  return (
    <>
      <main className="min-h-screen w-screen text-white flex flex-col sm:pt-0 transition-all duration-1000 ease-in-out relative">
        <BackgroundWrapper />
        <div className="relative z-10">
          <div className="max-w-[1100px] w-[90%] mx-auto mt-[40px] lg:mt-[40px] flex lg:flex-row flex-col items-center lg:justify-center">
            <div className="max-w-[436px] flex flex-col lg:items-start items-center text-white font-medium text-xl">
              <div className="w-full p-5 sm:p-9 rounded-xl bg-[#ffffff13] border border-[rgba(255,255,255,0.37)] relative">
                <h1 className="uppercase text-white font-[900] text-3xl mb-1">
                  300 Lost discs.
                </h1>
                <p className="text-white/90 text-base text-center sm:text-left sm:text-lg font-normal">
                  4 energy codes. One path to unlock the legend. Each disc holds
                  a fragment of the Legendary Code - echoing Astraea&apos;
                  memory, resonating with NADS, pulsing through the Monad
                  ecosystem, and sparking ancient legacy. Mint now to receive
                  one of four mysterious traits, each unlocking real benefits
                  from Monshape or its ecosystem partners.
                </p>
              </div>
            </div>
            {/* MINT BUTTON */}
            <div>
              <div className="w-full flex flex-col items-center justify-center mt-5 sm:mt-10">
                {address && isWrongNetwork ? (
                  <button
                    className="bg-[#858585] w-[60%] justify-center mx-auto flex items-center rounded h-[60px] sm:h-[85px] py-5 text-3xl uppercase text-white transition-all duration-300 ease-in-out mt-3"
                    onClick={handleSwitchNetwork}
                  >
                    Switch Network
                  </button>
                ) : null}

                {!address && (
                  <WalletModal open={open} setOpen={setOpen}>
                    <button
                      onClick={() => setOpen(true)}
                      className="bg-brandColor w-[60%] justify-center mx-auto flex items-center rounded h-[60px] sm:h-[85px] py-5 text-3xl uppercase text-white transition-all duration-300 ease-in-out mt-3"
                    >
                      Connect Wallet
                    </button>
                  </WalletModal>
                )}

                {address && !isWrongNetwork && (
                  <div className="flex items-center flex-col lg:flex-row gap-3 mt-3 w-full mb-0 uppercase">
                    {isSoldOut ? (
                      <button className="bg-[#858585] w-[60%] justify-center mx-auto flex items-center rounded h-[60px] sm:h-[85px] py-5 text-3xl uppercase text-white transition-all duration-300 ease-in-out mt-3">
                        Sold out!
                      </button>
                    ) : userCanMint ? (
                      <div className="flex items-center gap-5 w-full justify-center">
                        <button
                          className={`
                          ${
                            mintingStep === "idle" ||
                            mintingStep === "preparing" ||
                            mintingStep === "confirming"
                              ? "bg-[#A314B4] hover:bg-opacity-80"
                              : mintingStep === "success"
                              ? "bg-[#241F6F]"
                              : "bg-[#858585] cursor-not-allowed"
                          } 
                           w-[60%] justify-center mx-auto flex items-center rounded h-[60px] sm:h-[85px] py-5 text-3xl uppercase text-white transition-all duration-300 ease-in-out mt-3
                       
                        `}
                          onClick={handleMint}
                          disabled={
                            mintingStep !== "idle" && mintingStep !== "error"
                          }
                        >
                          {mintingStep === "preparing" && (
                            <div className="flex items-center gap-2">
                              Preparing...
                            </div>
                          )}
                          {mintingStep === "confirming" && (
                            <div className="flex items-center gap-2">
                              Confirmation...
                            </div>
                          )}
                          {mintingStep === "success" && (
                            <div className="flex items-center gap-2">
                              Success
                            </div>
                          )}
                          {mintingStep === "idle" && (
                            <>
                              <div className="font-medium w-full">MINT</div>
                            </>
                          )}
                        </button>
                      </div>
                    ) : (
                      <button className="bg-[#858585] w-[60%] justify-center mx-auto flex items-center rounded h-[60px] sm:h-[85px] py-5 text-3xl uppercase text-white transition-all duration-300 ease-in-out mt-3">
                        MINTED
                      </button>
                    )}
                  </div>
                )}

                <div className="mt-5 p-4 w-full sm:w-[60%] bg-[#241f6f32] rounded-md">
                  <div className="flex justify-between items-center mb-3">
                    <div
                      className={`pb-1 rounded-full flex items-center text-base sm:text-xl`}
                    >
                      <div
                        className={`${
                          isUserWL || isUserFCFS || isUserTeam
                            ? "bg-green-600"
                            : "bg-red-600"
                        } h-4 w-4 mr-3 `}
                      />

                      {isDisconnected
                        ? "NOT CONNECTED"
                        : isUserTeam
                        ? "ELIGIBLE TEAM"
                        : isUserWL
                        ? "ELIGIBLE WL"
                        : isUserFCFS
                        ? "ELIGIBLE FCFS"
                        : "NOT ELIGIBLE"}
                    </div>
                    {address && (
                      <div className="text-center">
                        <span className="text-base sm:text-xl text-gray-300 uppercase">
                          mints:
                        </span>
                        <span className="ml-2 text-base sm:text-xl text-white font-medium">
                          {userMints}/{maxMintsPerAddress}
                        </span>
                      </div>
                    )}
                  </div>

                  <div className="mt-3">
                    <div
                      className="w-full h-5 rounded overflow-hidden"
                      style={{
                        boxShadow: "0px 0px 13px 5px rgba(255, 255, 255, 0.05)",
                        background: "rgba(255, 255, 255, 0.1)",
                      }}
                    >
                      <div
                        className="h-full bg-[#2921a3] transition-all duration-500"
                        style={{
                          width: `${
                            (totalMinted / (maxSupply || 1000)) * 100
                          }%`,
                        }}
                      ></div>
                    </div>
                    <div className="flex justify-between items-center mt-2">
                      <span className="text-base sm:text-lg text-gray-200 uppercase">
                        Total minted
                      </span>
                      <span className="text-base sm:text-lg font-medium text-white">
                        {totalMinted || 0} / {maxSupply || 377}
                      </span>
                    </div>
                    <div className="h-0.5 w-full bg-[#7051eb38] my-4" />
                    <div className="flex items-center justify-between">
                      <p className="text-white font-medium text-base sm:text-lg mx-auto sm:text-[20px] mb-4 uppercase">
                        Current phase:{" "}
                        <span className="text-yellow-500">
                          {mintPhaseInfo?.currentPhase ===
                          "First Come First Served"
                            ? "FCFS"
                            : mintPhaseInfo?.currentPhase === "Team Only"
                            ? "TEAM"
                            : mintPhaseInfo?.currentPhase === "Whitelist"
                            ? "WHITELIST"
                            : "ENDED"}
                        </span>
                      </p>
                      {/* <p className="text-white font-bold text-lg sm:text-[22px] uppercase">
                        {!isSoldOut && (
                          <CountdownTimer
                            currentPhase={mintPhaseInfo?.currentPhase}
                          />
                        )}
                      </p> */}
                    </div>
                  </div>
                </div>
              </div>
              <UserNFTs />{" "}
            </div>
          </div>
        </div>
      </main>
      <AudioPlayer />
    </>
  );
}
